import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-otp',
  templateUrl: './otp.component.html',
  styleUrls: ['./otp.component.css']
})
export class OtpComponent  implements OnInit {
  @ViewChild('resendCodeForm') form: any;

  resendCodeText = true;
  timeLeft: any = 60;
  interval: any;

  public resendCodeDiv = false;
  public verificationCodeDiv = true;
  public identifier: string;
  enteredVerificationCode: string;
  enteredMobileNumber: string;
  returnUrl = '';
  public firstName: string;
  lastName: string;


  public constructor(
    private route: ActivatedRoute,
    private router: Router,
  ) {

  }
  ngOnInit() {}

  setTimer() {
    this.interval = setInterval(() => {
      if (this.timeLeft > 0) {
        this.timeLeft--;
        if (this.timeLeft === 0) {
          this.resendCodeText = false;
          clearInterval(this.interval);
        }
      } else {
        this.timeLeft = 60;
      }
    }, 1000);
  }

  resendVerificationCode() {
    this.resendCodeText = true;
    this.setTimer();
  }

  VerificationCodeSubmit() {
  
  }

  SubmitMobileNumber() {
    this.resendCodeText = true;
    this.setTimer();
    this.enteredVerificationCode = '';

    this.resendCodeDiv = true;
    this.verificationCodeDiv = false;
  
  }

  editMobileNumber() {
    if (this.timeLeft === 0) {
      this.verificationCodeDiv = true;
      this.resendCodeDiv = false;
    }
  }
}
