import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup,Validators } from '@angular/forms';
import { BlockUI, NgBlockUI } from 'ng-block-ui';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};


@Component({
  selector: 'app-profile-edit',
  templateUrl: './profile-edit.component.html',
  styleUrls: ['./profile-edit.component.css']
})
export class ProfileEditComponent implements OnInit {

  addForm: FormGroup;
  submitted = false;
  @BlockUI() blockUI: NgBlockUI;

   // Form fields
   get f() {
    return this.addForm.controls;
  }

  show_OtherStates: boolean = true;
  show_IndiaStates: boolean = false;
  show_Cities: boolean = false;
  show_RelationDp: boolean = false;
  listofCity: any;
  userInfo: any;
  partnerRefNo: any;
  referenceId: any = "";


  cities_list= [
    "SN Sarma",
    "Not In The List"
  ]


  constructor(private formBuilder: FormBuilder,public http: HttpClient,) { 

    this.addForm = this.formBuilder.group({
      contactNumber: ['', Validators.required],
      city: ['',Validators.required],
      state: ['', Validators.required],
      country: ['', Validators.required],
    });

  }

  sow_input = {
    User: {
      contactNumber: "", country: "",  state: "", city: "",
      
    }
  }

  ngOnInit() {

    this.userInfo = JSON.parse(localStorage.getItem("currentUserInfo"));
    if (this.userInfo) {
      this.userDetails = this.userInfo.details;
      this.partnerRefNo = this.userDetails.partner_ref_no;
      this.referenceId = this.userInfo.details.partner_ref_no;
      console.log(this.partnerRefNo + " " + this.referenceId);
    }

  }

  //based on country show state
  selectedCountry(event: any) {
    this.selectedCountryValue = event.target.value;
    console.log(this.selectedCountryValue);
    if (this.sow_input.User.country == "India" || this.sow_input.User.country == "india") {
      this.show_OtherStates = false;
      this.show_IndiaStates = true;
      //  this.sow_input.User.state="Select Any State"
    }
    else {
      this.show_IndiaStates = false;
      this.show_OtherStates = true;
      //  this.sow_input.User.state=""
    }
  }

  userDetails: any;
  cityResult: any;
  selectedCountryValue: any = "";
  //based on state show city
  selectedState(event: any) {
    this.selectedStateValue = event.target.value;
    console.log(this.selectedStateValue);

    if(this.selectedStateValue=="Select Any State"){
      this.show_Cities=false;  
    }

    //getCitybyState
    if (this.selectedStateValue !="Select Any State") {
      this.http.get('https://partnerportal.pidatacenters.com/slimportal/public/getcity?state_name=' + this.selectedStateValue, httpOptions).subscribe(
        data => {
          this.cityResult = data;
          this.listofCity = this.cityResult.cities_list;
          console.log(this.listofCity);//your data},
          this.show_Cities=true;
        },
        err => {
          console.log("error:")
        });
    }
  }

  selectedStateValue: any;
  selectedCityValue: any;

  selectedCity(event: any) {
    this.selectedCityValue = event.target.value;
    console.log("City value is:::>>"+this.selectedCityValue);
    this.show_RelationDp=true;
  }

  noValidationError: boolean = false;

  validation() {
    if (
    this.sow_input.User.contactNumber  && this.sow_input.User.country != "Country"
      && this.sow_input.User.state != "Select Any State"    && this.sow_input.User.city != "Select A City"
    ) {
      console.log("select");
      this.noValidationError = true;
      this.blockUI.stop();
    }
  }

  btnEditUpdate(){
    // this.blockUI.start();
    this.submitted = true;
    this.validation();

    let body = JSON.stringify({ contact_no: this.sow_input.User.contactNumber, country: this.sow_input.User.country, state: this.sow_input.User.state,city: this.sow_input.User.city });

    console.log(">>>><<<"+body);


  }

}
