import { Injectable } from '@angular/core';
declare var $: any;

// http://bootstrap-notify.remabledesigns.com/

@Injectable()
export class NotificationService {
  private notify;
  showNotification(
    from: any,
    align: any,
    errorMessage: string,
    colour: string
  ) {
    const type = [
      '',
      'info',
      'success',
      'warning',
      'danger',
      'rose',
      'primary'
    ];

    const color = Math.floor(Math.random() * 6 + 1);

    if (this.notify) {
      this.notify.close();
    }

    this.notify = $.notify(
      {
        icon: 'notifications',
        message: errorMessage
      },
      {
        type: colour ? colour : type[color],
        timer: 3000,
        placement: {
          from: from,
          align: align
        },
        newest_on_top: true
      }
    );
  }

  showErrorNotification(message: string) {
    this.showNotification('top', 'center', message, 'danger');
  }

  showSuccessNotification(message: string) {
    this.showNotification('top', 'center', message, 'success');
  }
}
