import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-testing',
  templateUrl: './testing.component.html',
  styleUrls: ['./testing.component.css']
})
export class TestingComponent implements OnInit {

  data: any;

  constructor() {
    this.data = {};
    this.data.isAllCollapsed = false;
    
    //List object having hierarchy of parents and its children
    this.data.ParentChildchecklist = [
      {
        id: 1,value: 'Parent LabelName1',
        childList: [
          {
            id: 1,value: 'child 1',isSelected: false
          },
          {
            id: 2,value: 'child 2',isSelected: false
          }
        ]
      },
      {
        id: 2,value: 'Parent LabelName2',
        childList: [
          {
            id: 1,value: 'child 1',isSelected: false
          },
          {
            id: 2,value: 'child 2',isSelected: false
          }
        ]
      },
      {
        id: 3,value: 'Parent LabelName3',
        childList: [
          {
            id: 1,value: 'child 1',isSelected: false
          },
          {
            id: 2,value: 'child 2',isSelected: false
          }
        ]
      }
    ];
  }
  ngOnInit() {

  }

  //Expand/Collapse event on each parent
  expandCollapse(data){
    data.isClosed = !data.isClosed;
  }

  //Master expand/ collapse event
  expandCollapseAll(data){
    for (var i = 0; i < data.ParentChildchecklist.length; i++) {
      data.ParentChildchecklist[i].isClosed = !data.isAllCollapsed;
    }
    data.isAllCollapsed = !data.isAllCollapsed;
  }


  getJsonData(){

    console.log(">>>>>json data>>>"+JSON.stringify(this.data));

  }

}
