import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Component } from "@angular/core";
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Router } from "@angular/router";
import { ApiServiceService } from "../allservices/apiservice.service";
import { single, price, serviceOffering } from './data';

const httpOptions = {
  headers: new HttpHeaders({"Content-Type" : "application/json"})
}


const data = {
  chart: {
    caption: "Website visits",
    subcaption: "Purchase - Conversion analysis for last year",
    decimals: "1",
    showvalues: "1",
    plottooltext: "$label: <b>$dataValue</b>",
    plotfillalpha: "70",
    theme: "fusion",
    streamlineddata: "0"
  },
  data: [
    {
      label: "Website Visits",
      value: "17362"
    },
    {
      label: "Downloads",
      value: "8039"
    },
    {
      label: "Checked Pricing",
      value: "5509"
    },
    {
      label: "Asked for Quotation",
      value: "3962"
    },
    {
      label: "Purchased",
      value: "2397"
    }
  ]
};


@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.css']
})




export class DashboardComponent {

  @BlockUI() blockUI : NgBlockUI;
//backed data interaction
  obj: any;
  userDetails: any = { partnerRefNo: "" };
  partnerRefNo: any;




  width = 400;
  height = 400;
  type = "funnel";
  dataFormat = "json";
  dataSource = data;


  //for number chart
  price: any[];
  
  priceview: any[] = [1200, 200];
  priceColorScheme = {
    domain: ['#5AA454', '#A10A28', '#C7B42C', '#AAAAAA']
  };
  priceCardColor: string = '#232837';


  //Service Offering Horizantal Bar chart
  serviceOffering: any[];
  serviceOfferingView: any[] = [,200];
  serviceOfferingcolorScheme = {
    domain: ['#5AA454', '#A10A28', '#C7B42C', '#AAAAAA']
  };
  // options
  gradient: boolean = true;
  showLegend: boolean = true;
  showLabels: boolean = true;
  isDoughnut: boolean = false;
  showXAxis: boolean = true;
  showYAxis: boolean = true;
  showXAxisLabel: boolean = true;
  yAxisLabel: string = 'Services Offering';
  showYAxisLabel: boolean = true;
  xAxisLabel: string = 'Service Offering Count';


  view: any[] = [, 200];
  single: any[];
    

    cardColor: string = '#232837';
  
    


      // options
 
  
    colorScheme = {
      domain: ['#5AA454', '#A10A28', '#C7B42C', '#AAAAAA']
    };
  
    constructor(private router: Router, public http:HttpClient, private apiService: ApiServiceService) {
      Object.assign(this, { single });
      Object.assign(this, { price });
      Object.assign(this, { serviceOffering });

    
      

    }
  
    onSelect(data): void {
      console.log('Item clicked', JSON.parse(JSON.stringify(data)));
    }
  
    onActivate(data): void {
      console.log('Activate', JSON.parse(JSON.stringify(data)));
    }
  
    onDeactivate(data): void {
      console.log('Deactivate', JSON.parse(JSON.stringify(data)));
    }
  

    
  ngOnInit() {

    this.obj = JSON.parse(localStorage.getItem('currentUserInfo'));
    if(this.obj === "" || this.obj == undefined || this.obj == null){
      this.router.navigate['/login'];
    }
    else{
      //console.log("test");
      this.blockUI.start();
      this.userDetails = this.obj.details;
      this.partnerRefNo = this.userDetails.partner_ref_no;
        let body = JSON.stringify({ partner_ref_id: this.partnerRefNo });
        //let body = JSON.stringify(this.partnerRefNo);
      this.http.post(this.apiService.getDashboardData(), body, httpOptions)
        .subscribe(
          data =>{
            console.log(data);
           
            this.blockUI.stop();
            //this.getPipelineResult(data); 
          },
          error => {
            console.log("error");
            //this.blockUI.stop();
            this.blockUI.stop();
          });

      console.log(this.userDetails.partner_ref_no);
    }



    setTimeout(function() {
      //code
    }, 700);

  }
}