
import { Component, OnInit, Input } from '@angular/core';
import { ApiServiceService } from '../allservices/apiservice.service';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { FormControl, Validators } from '@angular/forms';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { DialogserviceService } from '../dialog/dialogservice.service';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})

export class ProfileComponent implements OnInit {
  @BlockUI() blockUI: NgBlockUI;

  public openConfirmationDialog() {
    this.DialogserviceService.confirm('Conformation Dialog', 'Update Successfully')
      .then((confirmed) => console.log('User confirmed:', confirmed))
      .catch(() => console.log('User dismissed the dialog (e.g., by using ESC, clicking the cross icon, or clicking outside the dialog)'));
  }
  constructor(private DialogserviceService: DialogserviceService, private router: Router, public http: HttpClient, private apiservice: ApiServiceService) { }
  userData:any;
  obj:any;
  userInfo:any;
  partnerRefNo:any;
  userDetails:any;
  userEmail:any;
  loginRole:any;
  partnerDetails:any;

  ngOnInit() {
    this.blockUI.start();
    // this.userData = JSON.parse(this.obj);
    this.userInfo = JSON.parse(localStorage.getItem("currentUserInfo"));
    if(this.userInfo.details){
   this.userDetails = this.userInfo.details;
    this.partnerRefNo=this.userDetails.partner_ref_no;
    this.userEmail=this.userDetails.business_email;
    this.loginRole=this.userDetails.login_role;

    console.log(this.partnerRefNo +" "+this.userEmail);
    let body = JSON.stringify({
      reference_id:this.partnerRefNo,
      email:this.userEmail,
      login_role:this.loginRole
          }
      );
    console.log(body);
     this.http.post(this.apiservice.getProfileView(), body, httpOptions).subscribe(
      data => {
        console.log(data);
        this.getpartnerDetails(data);
        this.blockUI.stop();
      },
      error => {
        console.log("error");
      });
  }
}

Loc_Business:any;
Use_Case_Expertise:any;
Client_Base:any;
Technology_Expertise:any;
Professional_Services_Offered:any;
Interested_Ser_Prod_at_Pi:any;
  getpartnerDetails(data){
    if(data.details){
      this.partnerDetails=data.details[0];
      console.log(JSON.stringify(this.partnerDetails));
      if (this.partnerDetails.client_base) {
        this.Loc_Business = JSON.parse(this.partnerDetails.loc_o_b);
        console.log(this.Loc_Business);
      }
      if (this.partnerDetails.client_base) {
        this.Client_Base = JSON.parse(this.partnerDetails.client_base);
      }
      if (this.partnerDetails.use_case_expertise) {
        this.Use_Case_Expertise = JSON.parse(this.partnerDetails.use_case_expertise);
      }
      if (this.partnerDetails.technology_expertise) {
        this.Technology_Expertise = JSON.parse(this.partnerDetails.technology_expertise);
      }

      if (this.partnerDetails.professional_services_offered) {
        this.Professional_Services_Offered = JSON.parse(this.partnerDetails.professional_services_offered);
      }

      if (this.partnerDetails.interested_ser_prod_at_pi) {
        this.Interested_Ser_Prod_at_Pi = JSON.parse(this.partnerDetails.interested_ser_prod_at_pi);
      }

    }
  }

  btnCreatEditeProfile(){
    this.router.navigate(['/profileeditform']);
  }

}
