import { Component, OnInit, ChangeDetectorRef, Input, ViewChild } from '@angular/core';
import { ApiServiceService } from '../allservices/apiservice.service';
import { FormsModule } from '@angular/forms';
import { Router, ActivatedRoute, NavigationExtras } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NotificationService } from '../allservices/NotificationService.service';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  arrList: any = [];
  public data: any;
  userDetailData: any;
  title = 'Partner Project!';
  todaydate = new Date();
  userDetail = { email: "", password: "", };
  errorMessage = "";
  loginerrorMsg = "";
  loginResult: any;
  mobileLoginResult: any;
  mobileLoginVerifyResult: any;
  showNav: boolean = false;
  // this.userDetail.user="";
  constructor(private _apiservice: ApiServiceService, public notificationService: NotificationService,
    private cdRef: ChangeDetectorRef,
    public router: Router,
    public http: HttpClient,


    private route: ActivatedRoute,



  ) {
  }

  btnSubmit() {
    console.log("login");
    this.loginerrorMsg = "";
    if (!this.userDetail.email && !this.userDetail.password) {
      return this.errorMessage = "Please enter the email and password";
    }

    if (this.errorMessage == "") {
      let body = JSON.stringify({ email: this.userDetail.email, password: this.userDetail.password });
      console.log(body);
      this.http.post(this._apiservice.getLogin(), body, httpOptions).subscribe(
        data => {
          console.log(data);//your data},
          this.loginResult = data;
          if (this.loginResult.status == "err") {
            this.loginerrorMsg = this.loginResult.Message;
            return true;
          }
          if (this.loginResult.status == "success") {
            localStorage.removeItem('currentUserInfo');
            localStorage.setItem('currentUserInfo', JSON.stringify(this.loginResult));
            this._apiservice.CurrentSession = JSON.parse(localStorage.getItem('currentUserInfo'));
            this.cdRef.detectChanges();
            this.router.navigate(['/sow']);
            // this.router.navigate(['/login']);
          }
        },
        err => {
          alert("error")
        });
    }
  }

  ngOnInit() {


    this.showNav = true;



  }
  IsHidden = false;
  IsShow = true;

  onSelect() {
    this.IsHidden = !this.IsHidden;
    this.IsShow = !this.IsShow;


  }


  onSelected() {
    this.IsHidden = !this.IsHidden;
    this.IsShow = !this.IsShow;
  }

  @ViewChild('resendCodeForm') form: any;

  resendCodeText = true;
  timeLeft: any = 60;
  interval: any;

  public resendCodeDiv = false;
  public verificationCodeDiv = true;
  public identifier: string;
  enteredVerificationCode: string;
  enteredMobileNumber: string;
  returnUrl = '';
  public firstName: string;
  lastName: string;


  setTimer() {
    this.interval = setInterval(() => {
      if (this.timeLeft > 0) {
        this.timeLeft--;
        if (this.timeLeft === 0) {
          this.resendCodeText = false;
          clearInterval(this.interval);
        }
      } else {
        this.timeLeft = 60;
      }
    }, 1000);
  }

  resendVerificationCode() {
    this.resendCodeText = true;
    this.setTimer();

    let body = JSON.stringify({ mobile_num: this.enteredMobileNumber });
    console.log(body);
    this.http.post(this._apiservice.getMobileLogin(), body, httpOptions).subscribe(
      data => {
        console.log(data);//your data},
        this.mobileLoginResult = data;
        if (this.mobileLoginResult.status == "err") {
          this.notificationService.showErrorNotification(this.mobileLoginResult.Message);
          return true;
        }
        if (this.mobileLoginResult.status == "success") {
          // this.resendCodeDiv = true;
          // this.verificationCodeDiv = false;
          this.notificationService.showSuccessNotification(this.mobileLoginResult.Message);
        }
      },
      err => {
        alert("error")
      });
  }

  VerificationCodeSubmit() {

    let body = JSON.stringify({ mobile_num: this.enteredMobileNumber, verify_code: this.enteredVerificationCode });
    console.log(body);
    this.http.post(this._apiservice.getMobileLoginVerify(), body, httpOptions).subscribe(
      data => {
        console.log(data);//your data},
        this.mobileLoginVerifyResult = data;
        if (this.mobileLoginVerifyResult.status == "err") {
          this.notificationService.showErrorNotification(this.mobileLoginVerifyResult.Message);
          return true;
        }
        if (this.mobileLoginVerifyResult.status == "success") {
          localStorage.removeItem('currentUserInfo');
          localStorage.setItem('currentUserInfo', JSON.stringify(this.mobileLoginVerifyResult));
          this._apiservice.CurrentSession = JSON.parse(localStorage.getItem('currentUserInfo'));
          this.cdRef.detectChanges();

          this.router.navigate(['/sow']);
        }
      },
      err => {
        alert("error")
      });


  }

  SubmitMobileNumber() {
    this.resendCodeText = true;
    this.setTimer();
    this.enteredVerificationCode = '';

    let body = JSON.stringify({ mobile_num: this.enteredMobileNumber });
    console.log(body);
    this.http.post(this._apiservice.getMobileLogin(), body, httpOptions).subscribe(
      data => {
        console.log(data);//your data},
        this.mobileLoginResult = data;
        if (this.mobileLoginResult.status == "err") {
          console.log("error displayed");
          this.notificationService.showErrorNotification(this.mobileLoginResult.Message);
          return true;
        }
        if (this.mobileLoginResult.status == "success") {
          this.resendCodeDiv = true;
          this.verificationCodeDiv = false;
        }
      },
      err => {
        alert("error")
      });



  }

  editMobileNumber() {
    if (this.timeLeft === 0) {
      this.verificationCodeDiv = true;
      this.resendCodeDiv = false;
    }
  }
}
















