import { Component, OnInit, Input,ViewChild } from '@angular/core';
import { ApiServiceService } from '../allservices/apiservice.service';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BlockUI, NgBlockUI } from 'ng-block-ui';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Component({
  selector: 'app-gtm-marketingcollateral-form',
  templateUrl: './gtm-marketingcollateral-form.component.html',
  styleUrls: ['./gtm-marketingcollateral-form.component.css']
})
export class GtmMarketingcollateralFormComponent implements OnInit {

  @BlockUI() blockUI: NgBlockUI;
  obj: any;
  userData: any;
  GtmData = [];
  userInfo: any;
  selectedSowData = { potential: "", stage: "", sow_ref_id: "" }
  constructor(private router: Router, public http: HttpClient, private apiservice: ApiServiceService) { }
  userDetails: any={partnerRefNo:""};
  partnerRefNo:any;
  selectedGtm:any;
  GtmEventViewData:any;
  GtmEV_SuccessMetric:any;
  GtmEV_EventActivities:any;

  ngOnInit() {
        // this.showLoader = true;
    this.obj = localStorage.getItem('currentUserInfo');
    if (this.obj == null || this.obj == undefined || this.obj == "") {
      this.router.navigate(['/login']);
    }
    else {
    // this.show();
    this.blockUI.start();
    this.userData = JSON.parse(this.obj);
    this.userInfo = JSON.parse(localStorage.getItem("currentUserInfo"));
    this.selectedGtm = JSON.parse(localStorage.getItem("selectedGtm_storage"));
    if(this.userInfo.details){
    this.userDetails = this.userInfo.details;
    this.partnerRefNo=this.userDetails.partner_ref_no;
    console.log(this.selectedGtm);
    let body = JSON.stringify({request_no: this.selectedGtm.request_no});
    this.http.post(this.apiservice.getGtmMarketingView(), body, httpOptions).subscribe(
      data => {
        console.log(data);
        this.blockUI.stop();
           if(data){
            let gtmEventData : any=data;
           this.GtmEventViewData=gtmEventData.records[0];
           console.log(this.GtmEventViewData);
          }
           if(this.GtmEventViewData.success_metric && this.GtmEventViewData.success_metric.length>0){
           this.GtmEV_SuccessMetric= JSON.parse(this.GtmEventViewData.success_metric);
        }
        if(this.GtmEventViewData.promotional_activities && this.GtmEventViewData.promotional_activities.length>0){
        this.GtmEV_EventActivities= JSON.parse(this.GtmEventViewData.promotional_activities);
        }
        // $.fn.dataTable.ext.errMode = 'none';
      
      },
      error => {
        console.log("error");
        this.blockUI.stop();
      });
  }
  this.blockUI.stop();
}
  }

  showSowData:boolean=false;
  getGtmResult(data) {
  }

}
