import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { DataTable } from 'primeng/primeng';
import { ApiServiceService } from '../allservices/apiservice.service';
declare var $: any;
const httpOptions = {
  headers: new HttpHeaders({"Content-Type" : "application/json"})
}


@Component({
  selector: 'app-pipeline-list',
  templateUrl: './pipeline-list.component.html',
  styleUrls: ['./pipeline-list.component.css']
})


export class PipelineListComponent implements OnInit {
  @BlockUI() blockUI : NgBlockUI;
  @ViewChild('dataTable') table;
  public dataTable: DataTable;
  obj: any;
  userDetails: any = { partnerRefNo: "" };
  partnerRefNo: any;
  pipelineListData: [];
  showPipeline: boolean = false;


  constructor(private router: Router, public http:HttpClient, private apiService: ApiServiceService) { }

  ngOnInit() {

    this.obj = JSON.parse(localStorage.getItem('currentUserInfo'));
    if(this.obj === "" || this.obj == undefined || this.obj == null){
      this.router.navigate['/login'];
    }
    else{
      //console.log("test");
      this.blockUI.start();
      this.userDetails = this.obj.details;
      this.partnerRefNo = this.userDetails.partner_ref_no;
        let body = JSON.stringify({ partner_ref_id: this.partnerRefNo });
        //let body = JSON.stringify(this.partnerRefNo);
      this.http.post(this.apiService.getPipelineList(), body, httpOptions)
        .subscribe(
          data =>{
            console.log(data);
            this.blockUI.stop();
            this.getPipelineResult(data);
          },
          error => {
            console.log("error");
            //this.blockUI.stop();
            this.blockUI.stop();
          });

      console.log(this.userDetails.partner_ref_no);
    }



    setTimeout(function() {
      $('#datatables').DataTable({
        "aoColumnDefs": [
          {
            "bSortable": false,
            "aTargets": [ -1 ]
           }
       ],
        pagingType: 'full_numbers',
        lengthMenu: [[10, 25, 50, -1], [10, 25, 50, 'All']],

        responsive: true,
        language: {
          search: '_INPUT_',
          searchPlaceholder: 'Search records'
        }
      });


      $('.card .material-datatables label').addClass('form-group');
    }, 700);

  }

  getPipelineResult(data){
    if (data.records && data.records.length > 0) {
      this.showPipeline = true;
      this.pipelineListData = data.records;
      console.log(this.pipelineListData);
    }
  }
  btnCreatEditePipeline() {
    localStorage.removeItem('selectedPipelineStorage');
    this.router.navigate(['/pipelineform']);
  }

  btnPipelineView(selectedItem) {
    localStorage.setItem('selectedPipelineStorage', JSON.stringify(selectedItem));
    // console.log(selectedItem)
    this.router.navigate(['/pipelineview']);
  }

  btnEditPipeline(pipelineeditdata) {
    localStorage.removeItem('selectedPipelineStorage');
    localStorage.setItem('selectedPipelineStorage', JSON.stringify(pipelineeditdata));
    this.router.navigate(['/pipelineform']);
  }

}
