  GtmJMCArr = [];
  GtmJMCapArr = [];
  GtmArr = [];
  GtmArr1 = [];
  showSowData: boolean = false;
  getGtmResult(data) {
    if (data.gtm_fund_reqest && data.gtm_fund_reqest.length > 0) {
      this.GtmFRArr = data.gtm_fund_reqest;
    }
    if (data.gtm_joint_event_request && data.gtm_joint_event_request.length > 0) {
      this.GtmJEArr = data.gtm_joint_event_request;
    }
    if (data.gtm_joint_marketing_collateral && data.gtm_joint_marketing_collateral.length > 0) {
      this.GtmJMCArr = data.gtm_joint_marketing_collateral;
    }
    if (data.gtm_joint_media_campaign && data.gtm_joint_media_campaign.length > 0) {
      this.GtmJMCapArr = data.gtm_joint_media_campaign;
    }
    this.GtmArr = this.GtmFRArr.concat(this.GtmJEArr); // Merges both arrays
    this.GtmArr1 = this.GtmJMCArr.concat(this.GtmJMCapArr);
    this.GtmData = this.GtmArr.concat(this.GtmArr1);
    console.log("gtm data is::::::" + this.GtmData);
    this.dtTrigger.next();
  }

