  fundRequestUpload() {
    const formData: FormData = new FormData();
    formData.append('uploadfile', this.selectedFile, this.selectedFile.name);
    formData.append("email", this.userDetails.business_email);
    formData.append("partner_refer_id", this.partnerRefNo);
    console.log(formData);
    this.blockUI.start();
    this.http.post('https://partnerportal.pidatacenters.com/testapi/slimportal/public/partner/gtmfundfileupload', formData).subscribe(
      data => {
        this.uploadresult = data;
        console.log(this.uploadresult);

        if (this.uploadresult.status == "success") {
          this.blockUI.start();
          // this.fileMessage = this.uploadresult.message;
          this.notificationService.showSuccessNotification(this.uploadresult.message);
          console.log("sucess");
        }
        else {
          // this.fileMessage = this.uploadresult.message;
          this.notificationService.showErrorNotification(this.uploadresult.message);
          console.log("getting error");
          this.blockUI.stop();
        }
      },
      (err) => {
        console.log("error");
        this.blockUI.stop();
      }
    );
  }


