  //View Permission
  btnViewPermission(event, view) {
    this.blockUI.start();
    this.errorMsg = "";
    this.hideLabel = true;
    this.selectedUser = view;
    this.disabledInputs = true;
    this.hideAddLabel=false;
    this.hideEditLabel=false;
    this.hideViewLabel=true;
    console.log(view.assistant_id);
    if (view.assistant_id) {
      let body = JSON.stringify({ assistant_id: this.selectedUser.assistant_id, login_role: this.loginRole, partnerid: this.partnerRefNo });
      console.log(body);
      this.http.post(this.apiservice.getAssistView(), body, httpOptions).subscribe(
        data => {
          console.log(data);
          this.blockUI.stop();
          this.errorsms = data;
          if (this.errorsms.status == "success") {
            this.errorMsg = this.errorsms.message;
            this.getPermissionViewResult(data);
          }
          else {
            this.errorMsg = this.errorsms.message;
          }
        },
        error => {
          console.log("error");
        });
    }
  }

  getPermissionViewResult(data) {
    if (data.sow_records && data.sow_records.length > 0) {
      console.log(data.sow_records[0]);
      this.PermissionEditViewData = data.sow_records[0];
      // this.dtTrigger.next();
      if(this.PermissionEditViewData.permissions && this.PermissionEditViewData.permissions.length>0){
      this.PermissionCheckValues = data.sow_records[0].permissions;
      this.PermCheckValues = JSON.parse(data.sow_records[0].permissions);
      console.log(this.PermCheckValues);
      console.log(this.PermissionEditViewData.name + " " + this.PermCheckValues.sow.view);
      }
    }
  }
