  disabledButton: any;
  //Delete Permission
  btnDeletePermission() {
    this.blockUI.start();
    if (this.selUserforDelete.assistant_id) {
      let body = JSON.stringify({ assistant_id: this.selUserforDelete.assistant_id, login_role: this.loginRole, partnerid: this.partnerRefNo });
      console.log(body);
      this.http.post(this.apiservice.getDeleteAssist(), body, httpOptions).subscribe(
        data => {
          console.log(data);  
          this.deleteresult=data
          if(this.deleteresult.status=="Sucess"){
            this.delMessage=this.deleteresult.message;
          setTimeout(()=>{   
            this.getPermissionApiCall();
            this.blockUI.stop();
           jQuery("#deleteModal .close").click();
                this.delMessage="";
          }, 2000);
         }   
         else{
          this.delMessage=this.deleteresult.message;
         }    
        },
        error => {
          console.log("error");
          this.blockUI.stop();
        });
    }
    this.blockUI.stop();
  }

  delMessage:any="";
  deleteresult:any;
  //view permission end here
  ngOnInit() {
        this.dtOptions = {     
           processing: false,
           info: false
      };
      
      this.addForm = this.formBuilder.group({
      name: ['', Validators.required],
      mobile: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
  });

    this.blockUI.start();
    this.obj = localStorage.getItem('currentUserInfo');
    if (this.obj == null || this.obj == undefined || this.obj == "") {
      this.router.navigate(['/login']);
    }
    else {
      // this.show();
      this.userData = JSON.parse(this.obj);
      this.userInfo = JSON.parse(localStorage.getItem("currentUserInfo"));
      if (this.userInfo) {
        this.userDetails = this.userInfo.details;
        this.partnerRefNo = this.userDetails.partner_ref_no;
        this.loginRole = this.userDetails.login_role;
        console.log(this.partnerRefNo + " " + this.loginRole);
      }
      this.isActiveDiv = true;
      this.getPermissionApiCall();
    }
  }

  getPermissionApiCall() {
    let body = JSON.stringify({
      partnerid: this.partnerRefNo,
      // partnerid: "pi_partner_86752",
      login_role: this.loginRole
    }
    );
    console.log(body);
    this.http.post(this.apiservice.getAssistList(), body, httpOptions).subscribe(
      data => {
        console.log(data);
        this.blockUI.stop();
        this.getPermissionResult(data);
              this.dtTrigger.next();
      },
      error => {
        console.log("error");
        this.blockUI.stop();
      });
  }

  getPermissionResult(data) {
    if (data.sow_records && data.sow_records.length > 0) {
      this.showPermissionList = true;
      this.PermissionList = data.sow_records;
      console.log(this.PermissionList);
    }
  }

