  displayFnAccount(user?: Sow): string | undefined {
    return user ? user.contact_name : undefined;
  }

  private _filter(value: string): Sow[] {
    const filterValue = value.toLowerCase();

    return this.filteredBrands.filter(option => option['account_name'].toLowerCase().includes(filterValue));
  }




  onSelectionChanged(event: MatAutocompleteSelectedEvent) {
    console.log(event.option.value);
    for (var i = 0; i < this.filteredBrands.length; i++) {
      const question = this.filteredBrands[i];
      if (question.account_name == event.option.value) {
        this.school = question;
        console.log(JSON.stringify(this.school));
        this.sow_input.User.contactName = this.school.contact_name;
        this.sow_input.User.contactNumber = this.school.contact_number;
        this.sow_input.User.contactEmailID = this.school.contact_email;
      }
    }

  }

