  public focusoutHandler(event) {
    const inputValue = event.target.value;
    if (inputValue) {

      setTimeout(
        function() {
          const dialogRef = this.dialog.open(ConfirmationDialog,{
            data:{
              message: 'Bandwidth cost is not included in the probable revenue projection calculation',
              buttonText: {
                cancel: 'Ok'
              }
            }
          });
      
          dialogRef.afterClosed().subscribe((confirmed: boolean) => {
            if (confirmed) {
    
              const a = document.createElement('a');
              a.click();
              a.remove();
            }
          });
        }.bind(this),
        700
      );

    } else {
      console.log("entered value  null");
    }
  }

