  selectedStage(event: any) {

    if (event.target.value == "On Hold") {
      this.sow_input.User.probability = "10";

      // this.listofRating = ["Hot - Closure within 30 days"];

    } else if (event.target.value == "Prospect") {
      this.sow_input.User.probability = "30";

    } else if (event.target.value == "Proposal To Be Submitted") {
      this.sow_input.User.probability = "40";

    } else if (event.target.value == "Proposal Shared With Sales") {
      this.sow_input.User.probability = "40";

    } else if (event.target.value == "Proposal Submitted") {
      this.sow_input.User.probability = "50";

    } else if (event.target.value == "Negotiation Stage") {
      this.sow_input.User.probability = "60";

    } else if (event.target.value == "Shortlisted") {
      this.sow_input.User.probability = "70";

    } else if (event.target.value == "PO Received (Payment Pending)") {
      this.sow_input.User.probability = "80";

    } else if (event.target.value == "Closed Won") {
      this.sow_input.User.probability = "100";

    } else if (event.target.value == "Closed Dropped") {
      this.sow_input.User.probability = "0";

    } else if (event.target.value == "Closed Lost To Competition") {
      this.sow_input.User.probability = "0";

    }
  }

