import { Router } from '@angular/router';
import { Component, OnInit,ChangeDetectorRef } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ApiServiceService } from '../allservices/apiservice.service';
import { BlockUI, NgBlockUI } from 'ng-block-ui';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Component({
  selector: 'app-sowview',
  templateUrl: './sowview.component.html',
  styleUrls: ['./sowview.component.css']
})

export class SowviewComponent implements OnInit {
  @BlockUI() blockUI: NgBlockUI;

  userData:any;
  obj:any;
  userInfo:any;
  selectedPartner_storage:any;
  sowRefId="";
  constructor(public http: HttpClient,private cdRef:ChangeDetectorRef,  public router: Router,private apiservice:ApiServiceService) {
  }

  ngOnInit() {
    // this.cdRef.detectChanges();
    // this.showLoader = true;
    this.obj = localStorage.getItem('currentUserInfo');
    if (this.obj == null || this.obj == undefined || this.obj == "") {
      this.router.navigate(['/login']);
    }
    else {
      this.blockUI.start();
    this.userData = JSON.parse(this.obj);
    this.userInfo = JSON.parse(localStorage.getItem("currentUserInfo"));
    console.log(this.userInfo);
    this.selectedPartner_storage = JSON.parse(localStorage.getItem("selectedPartner_storage"));
    if(this.selectedPartner_storage){
      this.sowRefId=this.selectedPartner_storage.sow_ref_id;
      console.log(this.sowRefId);
    //getsowview
    let input={sow_ref_id: this.sowRefId };
    let body = JSON.stringify(input);
    return this.http.post(this.apiservice.getSowView(), body, httpOptions).subscribe(
      data => {
        this.blockUI.stop();
        console.log(data);
        this.getSowViewResult(data);
      },
      err => {
        console.log("error");
        this.blockUI.stop();
      });
    }
    }
    this.cdRef.detectChanges();
}

sowviewData:any;
SowRecord:any;
SowDoc:any;
showSowData:boolean=false;
getSowViewResult(data){
  this.showSowData=true;
  if(data.sow_records){
    this.sowviewData=data;
  this.SowRecord=this.sowviewData.sow_records[0];
  this.SowDoc=this.sowviewData.sow_docs;
}
}

}
