import { Component, OnInit, Input } from '@angular/core';
import { ApiServiceService } from '../allservices/apiservice.service';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { FormControl, Validators } from '@angular/forms';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { DialogserviceService } from '../dialog/dialogservice.service';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Component({
  selector: 'app-pipelineview',
  templateUrl: './pipelineview.component.html',
  styleUrls: ['./pipelineview.component.css']
})



export class PipelineviewComponent implements OnInit {
  @BlockUI() blockUI: NgBlockUI;

  public openConfirmationDialog() {
    this.DialogserviceService.confirm('Conformation Dialog', 'Update Successfully')
      .then((confirmed) => console.log('User confirmed:', confirmed))
      .catch(() => console.log('User dismissed the dialog (e.g., by using ESC, clicking the cross icon, or clicking outside the dialog)'));
  }
  constructor(private DialogserviceService: DialogserviceService, private router: Router, public http: HttpClient, private apiservice: ApiServiceService) { }
  userData:any;
  obj:any;
  userInfo:any;
  pipelineView:any=[];
  pipelineSingleView:any =[]
  records=[];
  partnerRefNo:any;
  userDetails:any;
  userEmail:any;
  loginRole:any;
  partnerDetails:any;
  unique_id_num:any;


  
  ngOnInit() {
    this.blockUI.start();
    // this.userData = JSON.parse(this.obj);
    this.userInfo = JSON.parse(localStorage.getItem("currentUserInfo"));
    this.pipelineView = JSON.parse(localStorage.getItem("selectedPipelineStorage"));

    if(this.userInfo.details && this.pipelineView){
   this.userDetails = this.userInfo.details;
    this.partnerRefNo=this.userDetails.partner_ref_no;
    this.userEmail=this.userDetails.business_email;
    this.loginRole=this.userDetails.login_role;
      this.unique_id_num =this.pipelineView.unique_id;
    console.log(this.partnerRefNo +" "+this.userEmail);
  
      let input={unique_id: this.unique_id_num };
      let body = JSON.stringify(input)
     this.http.post(this.apiservice.getPipelineView(), body, httpOptions).subscribe(
      data => {
        this.pipelineSingleView = data;
        console.log(this.pipelineSingleView)
        this.blockUI.stop();
      },
      error => {
        console.log("error");
      });
  }
}


  btnCreatEditeProfile(){
    this.router.navigate(['/profileeditform']);
  }
}
