import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ApiServiceService } from '../allservices/apiservice.service';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { DataTable } from 'primeng/primeng';
import { NotificationService } from '../allservices/NotificationService.service';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
declare var $: any;

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Component({
  selector: 'app-sow',
  templateUrl: './sow.component.html',
  styleUrls: ['./sow.component.css']
})
export class SowComponent implements OnInit {
  @BlockUI() blockUI: NgBlockUI;
  @ViewChild('dataTable') table;
  public dataTable: DataTable;
  // dtOptions: DataTables.Settings = {};
  // dtTrigger: Subject<any> = new Subject();
  obj: any;
  userData: any;
  SowSummaryData = [];
  userInfo: any;
  selectedSowData = { potential: "", stage: "", sow_ref_id: "" }
  constructor(private router: Router, public http: HttpClient, private apiservice: ApiServiceService, public element: ElementRef, public notificationService: NotificationService, private slimLoadingBarService: SlimLoadingBarService, ) { }
  userDetails: any = { partnerRefNo: "" };
  partnerRefNo: any;

  ngOnInit() {
    // this.dtOptions = {
    //   pagingType: 'full_numbers',
    //   pageLength: 5,
    //   processing: true,
    // };

    this.obj = localStorage.getItem('currentUserInfo');
    if (this.obj == null || this.obj == undefined || this.obj == "") {
      this.router.navigate(['/login']);
    }
    else {
      // this.show();
      this.blockUI.start();
      this.userData = JSON.parse(this.obj);
      this.userInfo = JSON.parse(localStorage.getItem("currentUserInfo"));
      if (this.userInfo.details) {
        this.userDetails = this.userInfo.details;
        this.partnerRefNo = this.userDetails.partner_ref_no;
        console.log(this.partnerRefNo);
        let body = JSON.stringify({ reference_id: this.partnerRefNo });
        this.http.post(this.apiservice.getSowSummary(), body, httpOptions).subscribe(
          data => {
            console.log(data);
            this.blockUI.stop();
            this.getSowSummaryResult(data);
            // this.dtTrigger.next();

          },
          error => {
            console.log("error");
            this.blockUI.stop();
          });
      }
      this.blockUI.stop();
    }

    setTimeout(function() {
      $('#datatables').DataTable({
        "aoColumnDefs": [
          { 
            "bSortable": false, 
            "aTargets": [ -1 ] // <-- gets last column and turns off sorting
           } 
       ],
        pagingType: 'full_numbers',
        lengthMenu: [[10, 25, 50, -1], [10, 25, 50, 'All']],
        
        responsive: true,
        language: {
          search: '_INPUT_',
          searchPlaceholder: 'Search records'
        }
      });


      $('.card .material-datatables label').addClass('form-group');
    }, 700);

  }

  showSowData: boolean = false;
  getSowSummaryResult(data) {
    if (data.sow_records && data.sow_records.length > 0) {
      this.showSowData = true;
      this.SowSummaryData = data.sow_records;
      console.log(this.SowSummaryData);
      for (var i = 0; i < this.SowSummaryData.length; i++) {
        if (this.SowSummaryData[i].closing_date == '0000-00-00 00:00:00') {
          this.zeroTime = true;
          this.SowSummaryData[i].closing_date = "";
        }
      }
    }

    // setTimeout(function () {
    //   $('#datatables').DataTable({
    //     destroy: true,
    //     "order": [],
    //     pagingType: 'full_numbers',
    //     lengthMenu: [[10, 25, 50, -1], [10, 25, 50, 'All']],
    //     responsive: true,
    //     language: {
    //       search: '_INPUT_',
    //       searchPlaceholder: 'Search Records'
    //     }
    //   });

    //   $('.card .material-datatables label').addClass('form-group');
    // }, 700);
  }

  zeroTime: boolean = false;
  //sow views
  btnSowView(selectedSow) {
    localStorage.removeItem('selectedPartner_storage');
    localStorage.setItem('selectedPartner_storage', JSON.stringify(selectedSow));
    console.log(selectedSow);
    this.router.navigate(['/sowview']);
  }

  editPage = true;
  //Edit Sow
  btnEditSowView(selectedSow) {
    localStorage.removeItem('selectedPartner_storage');
    localStorage.setItem('selectedPartner_storage', JSON.stringify(selectedSow));
    console.log(selectedSow);
    this.router.navigate(['/sowaddedit']);
  }

  //add sowaddedit
  btnCreatEditeSow() {
    localStorage.removeItem('selectedPartner_storage');
    this.router.navigate(['/sowaddedit']);
  }

  //avoid alert in datatable
  //   ngAfterViewInit(): void {this.dtTrigger.next();}
  //   dtElement:any;
  //   rerender(): void {
  //     this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
  //        dtInstance.destroy();
  //        this.dtTrigger.next();     
  //    });
  // }

  ngOnDestroy(): void {
    // this.dtTrigger.unsubscribe();
  }



}
