  convert(str) {
    const date = new Date(str),
      day = ('0' + date.getDate()).slice(-2),
      mnth = ('0' + (date.getMonth() + 1)).slice(-2);
    return [date.getFullYear(), mnth, day].join('-');
  }

  getContarctPeriodValue(cpValue){

    // this.pipeline.contractPeriodId= cpValue;

    console.log("entered cp value"+cpValue.source.value);


    console.log("ContarctPeriodValue Change");

    if(this.pipeline.ARC==0 && this.pipeline.MRC!=0 && this.pipeline.OTC!=0){

      console.log("arc  zero MRC,OTC not zero");

      this.pipeline.ACV = ((this.pipeline.MRC * this.pipeline.contractPeriodId)  /  this.pipeline.noOfYears) + this.pipeline.OTC;
      this.pipeline.TCV= this.pipeline.ACV * this.pipeline.noOfYears;
    }

    else if(this.pipeline.MRC==0 && this.pipeline.OTC==0 && this.pipeline.ARC!=0){

      console.log("arc not zero MRC,OTC zero");

      this.pipeline.ACV = this.pipeline.ARC;
      this.pipeline.TCV= this.pipeline.ACV * this.pipeline.noOfYears;

    }

    else if((this.pipeline.MRC!=0 && this.pipeline.OTC!=0 && this.pipeline.ARC!=0 )){

      console.log("arc MRC,OTC not zero");
      this.pipeline.ACV = ((this.pipeline.MRC * 12) + this.pipeline.ARC + this.pipeline.OTC);
      this.pipeline.TCV= this.pipeline.ACV * this.pipeline.noOfYears;

    }

  }

